import { css, excludeProps, styled, variants } from '@lokalise/styled';

import { color } from '../../utils/theme';

export const Container = styled.div.withConfig<{ height: number }>({
	shouldForwardProp: excludeProps(['height']),
})`
	width: 100%;
	height: ${({ height }) => `${height}px`};
	border-radius: ${({ height }) => `${height}px`};
	background-color: ${color('background.secondary')};
	overflow: hidden;
	box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.1);
`;

type ActiveProgressBarProps = {
	readonly progress: number;
	readonly height: number;
};

export const ActiveProgressBar = styled.div.withConfig<ActiveProgressBarProps>({
	shouldForwardProp: excludeProps(['height', 'progress']),
})`
	height: ${({ height }) => height}px;
	border-radius: 2px;
	width: ${({ progress }) => progress}%;

	${variants(
		({ progress }) => {
			switch (true) {
				case progress < 33:
					return 'low';
				case progress < 66:
					return 'medium';
				default:
					return 'high';
			}
		},
		{
			low: css`
				background: ${color('red.500')};
			`,
			medium: css`
				background: ${color('yellow.500')};
			`,
			high: css`
				background: ${color('primary.500')};
			`,
		},
	)}
`;

ActiveProgressBar.displayName = 'ActiveProgressBar';
